import Vue from "vue";
import Component from "vue-class-component";
import axios from "axios";

@Component
export default class ChatInput extends Vue {
  textarea = "";
  previews: any = [];
  file: any = "";
  arrImg: any = [];
  get tempMsg() {
    return this.$store.state.tempMsg;
  }
  get copyMsg() {    
    return this.$store.state.copyMsg;
  }
  toBase64(e: any) {
    const image = e.target.files;
    image.forEach((el: any) => {
      this.createBase64(el);
    });
  }
  createBase64(el: any) {
    const reader = new FileReader();
    reader.onload = (e) => {
      const img = e.target?.result;
      if (this.previews.length < 10) {
        this.previews.push({ image: img, id: Math.random() });
      }
    };
    console.log(this.tempMsg);
    reader.readAsDataURL(el);
  }
  hideBtn() {
    const inputFile: any = this.$refs.file;
    inputFile.click();
  }
  handleFileUpload() {
    const inputFile: any = this.$refs.file;
    if (inputFile.files) {
      this.file = inputFile.files;
    }
  }
  async submit() {
    if (
      this.textarea.trim() !== "" ||
      this.previews.length !== 0 ||
      this.arrImg.length !== 0
    ) {
      const arrImg: any = [];
      const formData = new FormData();
      if (this.file) {
        this.file.forEach((el: any) => {
          formData.append("picture", el);
        });
      }
      await axios
        .post("https://nest-zuz.herokuapp.com/pictures/upload", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(function(result) {
          result.data.forEach((el: any) => {
            arrImg.push({ image: el, id: Math.random() });
          });
        })
        .catch(function(err) {
          console.error(err);
        });
      this.arrImg = arrImg;
      if (this.tempMsg.length) {
        this.$socket.emit("sendMsg", {
          text: this.textarea,
          img: this.arrImg,
          quote: this.tempMsg,
          clientName: localStorage.getItem("clientName"),
          token: localStorage.getItem("token"),
        });
      } else if (this.copyMsg) {
        this.$socket.emit("sendMsg", {
          text: this.textarea,
          img: this.arrImg,
          quote: this.copyMsg,
          clientName: localStorage.getItem("clientName"),
          token: localStorage.getItem("token"),
        });
      }
    } else {
      alert("введи сообщение");
    }
    const form: any = this.$el.lastElementChild?.lastElementChild
      ?.lastElementChild?.lastElementChild;

    form.value = "";
    this.arrImg = [];
    this.textarea = "";
    this.previews = [];
    this.$store.commit("clearTempMessage");
  }
  bom() {
    this.$store.commit("clearTok");
    localStorage.removeItem("token");
    localStorage.removeItem("clientName");
    window.location.reload(false);
  }
  delitePreview(e: any) {
    console.log(this.arrImg);
    let spliceImg = 0;
    this.previews.forEach((el: any) => {
      if (el.id === +e.target.id) {
        spliceImg = el;
      }
    });
    if (this.tempMsg) {
      this.$store.commit("clearTempMessage");
    }
    if (this.previews.indexOf(spliceImg) !== -1) {
      this.previews.splice(this.previews.indexOf(spliceImg), 1);
    }
  }
}
