import Vue from "vue";
import Component from "vue-class-component";
import { mixins } from "vue-class-component";
import BigImg from "../BigImg/BigImg.vue";

const PropsMsg = Vue.extend({
  props: {
    id: Number,
    msg: Object,
    newMsgBtn: Function,
  },
});

@Component({
  components: {
    BigImg,
  },
})
export default class Msg extends mixins(PropsMsg, Vue) {
  displayOn = false
  get textMsg() {
    console.log(this.msg.msg);
    
    return this.msg.msg;
  }
  get clientName() {
    return this.msg.clientName;
  }
  get createAt() {
    return this.msg.created;
  }
  get myName() {
    return this.$store.state.myName;
  }
  get type() {
    return this.msg.type;
  }
  get countNoRead() {
    return this.$store.state.noRead.length;
  }
  get myImg() {
    return this.$store.state.myImg;
  }
  mounted() {
    if (
      this.$parent.$el.getBoundingClientRect().bottom <
      this.$el.getBoundingClientRect().top
    ) {
      this.newMsgBtn(this.countNoRead + 1);
      const allId: any[] = [];
      const msgs: any[] = [];
      this.$parent.$children.forEach((el: any) => {
        allId.push(el.id);
      });
      const filterNewId = allId.filter((el) => el === +this.$el.id);
      msgs.unshift(this);
      this.$store.dispatch("newNoRead", filterNewId[0]);
    }
  }
  clickedEl(e: any) {
    console.log(this.$children[0]);
    const children: any =  this.$children[0]
   children.bigImg(e.target.src);
  }
  dropdown(e: any) {
    this.displayOn = !this.displayOn
    console.log(this.textMsg);
    
  }
  copyMsg() {
    this.$store.commit("copyMsg", this.msg.msg);
    this.displayOn = !this.displayOn
  }
  quoteMsg() {
    this.$store.commit("tepmMessage", this.msg.msg);
    this.displayOn = !this.displayOn
  }
}
