import Vue from "vue";
import Vuex from "vuex";

interface Msg {
  clientName: string;
  msg: string;
  id: number;
  read: boolean;
  myImg: string;
}

Vue.use(Vuex);
const arrMsg: Msg[] = [];


export default new Vuex.Store({
  state: {
    arrMsg,
    roomList: [],
    myName: "",
    newUser: [],
    noRead: [],
    myImg: "",
    userImg: "",
    token: "",
    tempMsg: [],
    copyMsg: []
  },
  mutations: {
    /* eslint-disable */
    clearTok(state) {
      state.token = "";
      state.myName = "";
    },
    clearTempMessage(state) {
      state.tempMsg = [];
      state.copyMsg = [];
    },
    copyMsg(state, payload){
      let copyMsg:any = state.copyMsg
       copyMsg = [];
      const msg = {
        text: payload.text,
        img: payload.img,
        name: payload.clientName
      }
      copyMsg.push(msg);
    },
    tepmMessage(state, payload) {
      let tempMsg:any = state.tempMsg
      const msg = {
        text: payload.text,
        img: payload.img,
        name: payload.clientName
      }
      tempMsg.push(msg);
    },
    SOCKET_accept(state, payload) {
      state.myName = payload.clientName;
      state.token = payload.token;
      localStorage.setItem("token",  payload.token);
      localStorage.setItem("clientName", payload.clientName);
    },
    SOCKET_newMsg(state, payload) {
      const userImg = "";
      const msg = {
        id: Math.random(),
        created: new Date(),
        type: "userMsg",
        ...payload,
        read: true,
        myImg: "",
        userImg: userImg,
      };
      state.arrMsg.unshift(msg);
    },
    newRoomList(state, payload) {
      state.roomList = payload;
    },
    SOCKET_newUserConnected(state, payload) {
      const msg = {
        id: Math.random(),
        created: new Date().toString(),
        type: "systemMsg",
        msg: `Вошёл в чат ${payload.clientName}`,
        clientName: "system",
        read: true,
        myImg: "",
      };
      state.arrMsg.unshift(msg);
    },
    SOCKET_userDisconnected(state, payload) {
      const msg = {
        id: Math.random(),
        created: new Date().toString(),
        type: "systemMsg",
        msg: `Вышел из чата ${payload.clientName}`,
        clientName: "system",
        read: true,
        myImg: "",
      };
      state.arrMsg.unshift(msg);
    },
    newMsg(state, payload) {
      state.arrMsg.forEach((el) => {
        /*         if (el.id === payload && el.read === true) {
          el.read = false;
          state.noRead.push(el);
          console.log(state.noRead);
        }
 */
      });
    },
    myImg(state, payload) {
      state.myImg === "" ? (state.myImg = payload) : state.myImg;
    },
    spliceId(state, payload) {
      state.arrMsg.forEach((el) => {
        if (el.read === false) {
          console.log("FALSE READ", el);
        }
      });
    },
  },

  actions: {
    SOCKET_auth({ commit }, payload) {
      commit("auth", payload);
    },
    SOCKET_connected({ commit }, payload) {
      localStorage.getItem("token")
        ? commit("newUserConnect", payload)
        : alert("No token");
    },
    newNoRead({ commit }, payload) {
      commit("newMsg", payload);
    },
    myNewStateImg({ commit }, payload) {
      commit("myImg", payload);
    },
    splice({ commit }, payload) {
      commit("spliceId", payload);
    },
    SOCKET_roomList({ commit }, payload) {
      let images: { name: any; image: any }[] = [];
      payload.list.forEach(async (el: any) => {
        fetch("https://randomuser.me/api/")
          .then((response) => {
            return response.json();
          })
          .then((data) => {
            images.push({
              name: el,
              image: data.results[0].picture.thumbnail,
            });
          })
          .then(() => {
            images.forEach((el) => {
              if (el.name === this.state.myName) {
                this.state.myImg = el.image;
              }
            });
          });
      });
      commit("newRoomList", images);
    },
  },
  modules: {},
});
