import Vue from "vue";
import Component from "vue-class-component";
import Msg from "../Msg/Msg.vue";

import { mixins } from "vue-class-component";

const PropsMsg = Vue.extend({
  props: {
    splice: Function,
  },
});
@Component({
  components: {
    Msg,
  },
})
export default class ChatArea extends mixins(PropsMsg, Vue) {
  counter = 0;
  newMsg = false;
  arr: any[] = [];
  get msgs() {
    return this.$store.state.arrMsg;
  }
  get newUser() {
    return this.$store.state.newUser;
  }
  get usersName() {
    return this.$store.state.userName;
  }
  newMsgBtn(counter: any) {
    this.counter = counter;
    this.newMsg = true;
  }
  toBottom() {
    this.newMsg = false;
    this.$el.scrollTop = 0;
    this.counter = 0;
  }
  allMsgRead() {
    this.msgs.forEach((el: any) => {
      el.read = true;
    });
    console.log(this.msgs);
  }
  mounted() {
    this.$children.forEach((el) => this.arr.push(el));
  }
  scroll() {
    const filterNewId = this.arr.filter(
      (el) =>
        el.$el.getBoundingClientRect().bottom >
        this.$el.getBoundingClientRect().bottom
    );
    this.$store.dispatch("splice", filterNewId);
    if (this.$el.scrollTop === 0) {
      this.allMsgRead();
      this.counter = 0;
      this.newMsg = false;
    }
  }
}
