import Vue from "vue";
import Router from "vue-router";
import AuthWindow from "../AuthWindow/AuthWindow.vue";
import WrapperChat from "../wrapperChat/wrapperChat.vue";

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "auth",
      component: AuthWindow,
    },
    {
      path: "/chat",
      name: "chat",
      component: WrapperChat,
    },
  ],
});
const storage = localStorage;

router.beforeEach((to, from, next) => {
  if (to.name !== "/chat" && !storage.getItem("token") && from.name !== null) {  
    next({ name: "auth" });
  } else next();
});
router.beforeEach((to, from, next) => {
  if (to.name !== "chat" && storage.getItem("token")) {
    next({ path: "/chat" });
  } else next();
});
router.beforeEach((to, from, next) => {
    if (to.name !== "auth" && !storage.getItem("token")) {
      next({ path: "/" });
    } else next();
  });
export default router;
