import Vue from "vue";
import Component from "vue-class-component";
import { mixins } from "vue-class-component";

const PropsMsg = Vue.extend({
  props: {
    msg: Object,
  },
});

@Component
export default class BigImg extends mixins(PropsMsg, Vue) {
  bigImgUrl: any = [];
  img = "";
  log() {
    const message: any = this.$parent;
    message.msg.msg.img.forEach((el: any) => {
      const url = el.image.path;
      this.bigImgUrl.push(url);
    });
    this.bigImgUrl.forEach((el: any) => {
      console.log(this);
    });
  }
  bigImg(url: any) {
    if (this.bigImgUrl.length === 0) {
      this.log();
    }
    this.bigImgUrl.forEach((el: any) => {
      el === url ? this.img = el : console.log(this.img);
    });
  }
  hideImg() {
      this.img = '';
  }
}
